@import "@/assets/variables";

.main {
  display: block;
  flex-grow: 1;
}

.row {
  display: flex;
  align-items: flex-start;
}

.content {
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: flex-start;
}

.sidebar {
  background: var(--secondary-bg);
  border-radius: 8px;
  padding: 10px;
  margin-left: 15px;
  flex: 0 0 200px;

  @include notebook {
    display: none;
  }

  &__title {
    color: var(--text-primary);
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 15px;
  }

  &__button {
    background: #fff5f5;
    border: 1px solid rgba(250, 101, 101, 0.5);
    border-radius: 2px;
    padding: 5px 20px;
    margin-top: 10px;
    font-size: 14px;
    display: inline-block;
  }
}
